import { Component, OnInit } from "@angular/core";
import { Router, ActivatedRoute } from "@angular/router";
import {
  FormGroup,
  FormBuilder,
  Validators,
  FormControl,
  FormArray,
} from "@angular/forms";
import {
  multiSelectConfig,
  constants,
} from "../../../../core/constants/config";
import _ from "lodash";
import { DoctorService } from "../../services/doctor.service";
import { AlertService } from "src/app/core/_alert";
import { Location } from "@angular/common";
import { CommonService } from "src/app/core/services/common.service";

@Component({
  selector: "app-doctor-profile",
  templateUrl: "./doctor-profile.component.html",
  styleUrls: ["./doctor-profile.component.css"],
  styles: [
    `
      /deep/ .multiselect-dropdown .dropdown-btn {
        font-size: 0.9375rem !important;
        color: #b5b5b5 !important;
        font-family: "Poppins", sans-serif !important;
        font-weight: 400 !important;
        border: 1px solid #d2d2d2 !important;
        padding: 10px 12px !important;
        height: auto !important;
        border-radius: 9px !important;
      }
    `,
  ],
})
export class DoctorProfileComponent implements OnInit {
  doctorForm: FormGroup;
  isSubmitted = false;
  masterData;
  metaData;
  disableBtn = false;
  isZipCodeCorrect = "";
  public countiesSettings = {
    ...multiSelectConfig.countiesConfig,
    ...multiSelectConfig.common,
  };
  public insuranceSettings = {
    ...multiSelectConfig.insuranceConfig,
    ...multiSelectConfig.common,
  };
  public expertiseSettings = {
    ...multiSelectConfig.expertiseConfig,
    ...multiSelectConfig.common,
  };
  doctorId = "";
  doctor;
  selectedTimeFrames;
  allowUser = "";
  constructor(
    private router: Router,
    private fb: FormBuilder,
    private route: ActivatedRoute,
    private doctorService: DoctorService,
    private alertService: AlertService,
    private location: Location,
    public common: CommonService
  ) {
    this.checkRoutes();
    this.initForm();
    this.fetchQueryParams();
    this.getResolverData();
  }

  /**
   * @description get resolver data from the activated route
   */
  getResolverData() {
    // metadata data list from resolver
    this.masterData = this.route.snapshot.data.metaData;
    this.createTimeFramesControl(this.masterData.data.timeframes);
  }

  //initialize doctor form controls
  initForm() {
    this.doctorForm = this.fb.group({
      name: ["", [Validators.required]],
      email: [
        "",
        [
          Validators.required,
          Validators.email,
          Validators.pattern("^[a-z0-9._%+-]+@[a-z0-9.-]+\\.[a-z]{2,4}$"),
        ],
      ],
      counties: new FormControl("", Validators.required),
      insurances: new FormControl("", Validators.required),
      expertise: new FormControl("", Validators.required),
      phoneNumber: [
        "",
        [
          Validators.required,
          Validators.minLength(10),
          Validators.maxLength(10),
        ],
      ],
      isAvailable: ["", Validators.required],
      counselingMethod: ["", [Validators.required]],
      license: new FormControl("", [Validators.required]),
      gender: ["", Validators.required],
      // zipcode: [
      //   "",
      //   [Validators.required, Validators.minLength(5), Validators.maxLength(9)],
      // ],
      // city: ["", Validators.required],
      timeframes: this.fb.array([]),
    });
  }

  //fetch id from query params
  fetchQueryParams() {
    this.route.queryParams.subscribe((data) => {
      if (data && data.id) {
        this.doctorId = data.id;
        this.getDoctorsDetails(this.doctorId);
      }
    });
  }

  //get doctor details using doctor id
  getDoctorsDetails(id) {
    this.doctorService.getDoctorDetails(id).subscribe((data) => {
      if (
        this.allowUser === "EDIT" &&
        data.data.doctors.verifiedByAdmin === false
      ) {
        this.router.navigateByUrl("admin/clinicians/new-requests");
      }
      this.doctor = data.data.doctors;
      this.setFormVals(this.doctor);
    });
  }

  ngOnInit() {}

  /**
   * create form array control for time frames
   * @param timeFrameInput array containing time frame values
   */
  createTimeFramesControl(timeFrameInput) {
    timeFrameInput.map((timeFrame) => {
      (this.doctorForm.get("timeframes") as FormArray).push(
        new FormControl(false)
      );
    });
  }

  checkRoutes() {
    if (this.router.url.split("/")[3].split("?")[0] === "edit-clinician") {
      this.allowUser = "EDIT";
    } else if (this.router.url === "/admin/clinicians/add-clinician") {
      this.allowUser = "ADD";
    } else {
      this.allowUser = "VIEW";
      this.insuranceSettings.enableCheckAll = false;
      this.expertiseSettings.enableCheckAll = false;
      this.countiesSettings.enableCheckAll = false;
      this.countiesSettings.allowSearchFilter = false;
      this.insuranceSettings.allowSearchFilter = false;
      this.expertiseSettings.allowSearchFilter = false;
    }
  }

  // when form is submitted
  submitForm() {
    this.isSubmitted = true;
    if (this.doctorForm.valid && this.isTimeFrameValid) {
      this.getSelectedTimeFrames();

      let body = this.alterFormData(this.doctorForm.value);
      if (this.allowUser === "EDIT") {
        this.editUser(body);
      } else {
        this.addUser(body);
      }
    }
  }

  // add a new clinician
  addUser(body) {
    this.disableBtn = true;
    this.doctorService.addDoctor(body).subscribe(
      (data) => {
        this.isSubmitted = false;
        this.alertService.success(data.message);
        setTimeout(() => {
          this.router.navigateByUrl("admin/clinicians");
        }, 5000);
      },
      (error) => {
        this.isSubmitted = false;
        this.disableBtn = false;
      }
    );
  }

  //  update the existing clinician
  editUser(body) {
    this.doctorService.updateDoctor(this.doctorId, body).subscribe(
      (data) => {
        this.alertService.success(data.message);
      },
      (error) => {
        this.isSubmitted = false;
      }
    );
  }

  //alter form data according to backend requirement
  alterFormData(data): Object {
    let insurances = [];
    let expertises = [];
    let counties = [];
    data.insurances.forEach((insurance) => {
      if (insurance.insurance) {
        insurances.push(insurance.id);
      } else {
        insurances.push(insurance);
      }
    });
    data.expertise.forEach((expertise) => {
      if (expertise.expertise) {
        expertises.push(expertise.id);
      } else {
        expertises.push(expertise);
      }
    });
    data.counties.length
      ? data.counties.forEach((county) => {
          if (county.county) {
            counties.push(county.id);
          } else {
            counties.push(county);
          }
        })
      : "";
    data.insurances = insurances;
    data.expertise = expertises;
    data.counties = counties;
    if (this.allowUser === "EDIT") {
      delete data.email;
    }
    return data;
  }

  //  get and set user selected time frames into array
  getSelectedTimeFrames() {
    const mappedTimeFramesArr = _.map(
      this.doctorForm.controls.timeframes["controls"],
      (timeFrame, i) => {
        return timeFrame.value && this.masterData.data.timeframes[i].id;
      }
    );
    //set and create array of only selected time frames
    this.selectedTimeFrames = _.filter(mappedTimeFramesArr, function (
      timeFrame
    ) {
      if (timeFrame !== false) {
        return timeFrame;
      }
    });
    this.doctorForm.value["timeframes"] = this.selectedTimeFrames;
  }

  get f() {
    return this.doctorForm.controls;
  }

  //handle time frame checkboxes
  handleTimeFrameChecks(value, index) {
    if (value.id === constants.TIMEFRAME_ANYTIMEID) {
      let tempArr = [false, false, false, false, false];
      tempArr[index] = !this.doctorForm.value.timeframes[index];
      (this.doctorForm.get("timeframes") as FormArray).setValue(tempArr);
    } else {
      let anyTimeIndex = this.masterData.data.timeframes.findIndex((val) => {
        return val.id === constants.TIMEFRAME_ANYTIMEID;
      });
      (this.doctorForm.get("timeframes") as FormArray).value[
        anyTimeIndex
      ] = false;
    }
  }

  //fill form fields for edit clinician
  setFormVals(data) {
    let tempArr = [false, false, false, false, false];

    data.timeframes.forEach((timeFrame) => {
      for (let i = 0; i < this.masterData.data.timeframes.length; i++) {
        if (this.masterData.data.timeframes[i].id === timeFrame.id) {
          tempArr[i] = true;
        }
      }
    });
    this.doctor.counselingMethod = Object.keys(this.doctor.counselingMethod)[0];
    this.doctor.license = this.doctor.license.id;
    this.doctorForm.patchValue(data);
    this.doctorForm.get("expertise").patchValue(data.expertises);
    this.doctorForm.get("timeframes").patchValue(tempArr);
  }

  get isTimeFrameValid() {
    let timeFrames = this.doctorForm.get("timeframes").value;
    let validTimeFrames = timeFrames.filter((reason) => {
      return reason != false && reason != null;
    });
    if (this.isSubmitted === true) {
      if (validTimeFrames && validTimeFrames.length) {
        return true;
      } else {
        return false;
      }
    }
  }

  // check for only number entered
  onlyNumber(event) {
    const charCode = event.query ? event.query : event.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57)) {
      return false;
    }

    return true;
  }

  // get county name based on  zip code
  getCounty(event) {
    if (this.doctorForm.get("zipcode").valid) {
      this.doctorForm.get("county").reset();
      this.doctorForm.get("countyName").reset();
      this.doctorService.getCountyName(event.target.value).subscribe(
        (data) => {
          this.isZipCodeCorrect = "true";
          this.doctorForm.get("countyName").setValue(data.data[0].county);
          this.doctorForm.get("county").setValue(data.data[0].id);
        },
        (error) => {
          this.isZipCodeCorrect = "false";
        }
      );
    }
  }
  changeZipCode(event) {
    this.doctorForm.get("countyName").reset();
    this.isZipCodeCorrect = "";
  }

  backPage() {
    this.location.back();
  }
}
