import {
  Component,
  OnInit,
  Input,
  OnChanges,
  Output,
  EventEmitter,
} from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MetaDataService } from "../services/meta-data.service";
import { Router, ActivatedRoute } from "@angular/router";
import { AlertService } from "src/app/core/_alert";
import { DialogService } from "../services/dialog.service";
import { CommonService } from "src/app/core/services/common.service";

@Component({
  selector: "app-manage-meta-data",
  templateUrl: "./manage-meta-data.component.html",
  styleUrls: ["./manage-meta-data.component.css"],
})
export class ManageMetaDataComponent implements OnInit {
  @Input() data;
  form: FormGroup;
  masterData = [];
  currRoute = "";
  isDisabled = false;
  allowUser = "";
  isSubmitted = false;
  id = "";
  @Output() status: EventEmitter<any> = new EventEmitter();

  constructor(
    private fb: FormBuilder,
    private metaService: MetaDataService,
    private router: Router,
    private alertService: AlertService,
    private route: ActivatedRoute,
    private dialog: DialogService,
    private common: CommonService
  ) {
    this.form = this.fb.group({
      name: ["", [Validators.required]],
    });

    setTimeout(() => {
      this.currRoute = this.data.currURL;
      if (this.data.type === "EDIT") {
        this.form.get("name").setValue(this.data.data.name);
      }
    });
  }

  // get details from id for updation
  getDetails(id) {
    this.metaService
      .getDetails(this.currRoute, this.id)
      .subscribe((data) => {});
  }

  ngOnInit() {}

  //when user submits form
  submitForm() {
    this.isSubmitted = true;
    if (this.form.valid) {
      this.isDisabled = true;
      if (this.data.type === "ADD") {
        this.addData();
      } else {
        this.updateData();
      }
    }
  }

  // add new metadata
  addData() {
    if (this.form.valid) {
      this.metaService.addMetaData(this.currRoute, this.form.value).subscribe(
        (data) => {
          this.dialog.close();
          this.status.emit(true);
          this.alertService.success(data.message);
        },
        (error) => {
          this.isDisabled = false;
        }
      );
    }
  }

  // update the data
  updateData() {
    if (this.form.valid) {
      this.metaService
        .updateMetaData(this.data.data.id, this.currRoute, this.form.value)
        .subscribe((data) => {
          this.dialog.close();
          this.status.emit(true);
          this.alertService.success(data.message);
        }),
        (error) => {
          this.isDisabled = false;
        };
    }
  }

  get f() {
    return this.form.controls;
  }
}
