import {
  Component,
  OnInit,
  Input,
  OnChanges,
  Output,
  EventEmitter,
} from "@angular/core";
import { constants } from "../../../../core/constants/config";

@Component({
  selector: "app-pagination",
  templateUrl: "./pagination.component.html",
  styleUrls: ["./pagination.component.css"],
})
export class PaginationComponent implements OnInit, OnChanges {
  @Input() totalRecords;
  @Input() data;
  @Input() currentPage;
  recordsPerPage = constants.paginationRecordsPerPage;
  @Output() clickedPage = new EventEmitter();
  pages;
  pageNumbers = [];
  constructor() {}

  ngOnInit() {}

  ngOnChanges(changes: import("@angular/core").SimpleChanges): void {
    let allPageNumbers = [];
    this.pages = Math.ceil(this.totalRecords / this.recordsPerPage);
    // this.pageNumbers = [{}, ...allPageNumbers, {}];
    if (this.currentPage < 3) {
      for (let i = 1; i <= this.pages; i++) {
        allPageNumbers.push({
          linkTo: i,
          text: i,
        });
        if (allPageNumbers.length >= 3) {
          break;
        }
      }
    } else {
      if (this.currentPage != this.pages) {
        for (let i = this.currentPage - 1; i <= this.pages; i++) {
          allPageNumbers.push({
            linkTo: i,
            text: i,
          });
          if (allPageNumbers.length >= 3) {
            break;
          }
        }
      } else {
        for (let i = this.currentPage - 2; i <= this.currentPage; i++) {
          allPageNumbers.push({
            linkTo: i,
            text: i,
          });
        }
      }
    }
    this.pageNumbers = [{}, ...allPageNumbers, {}];
  }

  goTo(page) {
    this.currentPage = page.linkTo <= 0 ? 1 : page.linkTo;
    this.currentPage = page.linkTo > this.pages ? this.pages : this.currentPage;
    this.clickedPage.emit(this.currentPage);
  }
}
