import { Component, ElementRef, OnInit, ViewChild } from "@angular/core";
import {
  FormGroup,
  FormBuilder,
  Validators,
  FormControl,
  FormArray,
} from "@angular/forms";
import _ from "lodash";
import { PatientService } from "../../services/patient.service";
import { AlertService } from "src/app/core/_alert/alert.service";
import { constants } from "../../../core/constants/config";
import { CommonService } from "src/app/core/services/common.service";
import { countryCodes } from "../../../core/constants/countryCodes";
import { NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { OtpVerificationComponent } from "../otp-verification/otp-verification.component";
import { Observable } from "rxjs";
import * as moment from "moment";
import * as config from "../../../core/constants/config";

@Component({
  selector: "app-enquiry-form",
  templateUrl: "./enquiry-form.component.html",
  styleUrls: ["./enquiry-form.component.css"],
})
export class EnquiryFormComponent implements OnInit {
  @ViewChild("fileInput", { static: true }) insuranceFileInput;
  enquiryForm: FormGroup;
  reasons = [];
  disableButton = false;
  timeFrames = [];
  masterData: any;
  selectedReasons;
  selectedTimeFrames;
  isSubmitted = false;
  currReasons = [];
  preferences = [];
  public maxDate;
  isOtherExperty = false;
  cities = [];
  isZipCodeCorrect = "";
  countryCodes = countryCodes;
  invalidPhoneNumber = false;
  wrongOTP = false;
  insuranceFile: File = null;
  constructor(
    private fb: FormBuilder,
    private patientService: PatientService,
    private alert: AlertService,
    public common: CommonService,
    private modal: NgbModal
  ) {}

  ngOnInit() {
    this.initForm();
    this.getMasterData();
    this.maxDate = moment(new Date()).format("YYYY-MM-DD");
  }

  //get form master data from backend
  getMasterData() {
    this.patientService.getEnquiryMasterData().subscribe(
      (data) => {
        this.masterData = data;

        //create form control for timeframe field
        this.createTimeFramesControl(this.masterData.data.timeframes);
        //create form control for reasons
        this.createReasonsControl(this.masterData.data.expertise);
      },
      (err) => {}
    );
  }

  /**
   * initialize enquiry form
   */
  initForm() {
    this.enquiryForm = this.fb.group({
      clientName: ["", [Validators.required, Validators.minLength(3)]],
      dob: ["", [Validators.required]],
      phoneNumber: [
        "",
        [
          Validators.required,
          Validators.minLength(10),
          Validators.maxLength(10),
        ],
      ],
      email: [
        "",
        [
          Validators.required,
          Validators.pattern(
            "^[a-zA-Z0-9][-a-zA-Z0-9._]+@([- a-z0-9]+[.])+[a-z]{2,3}$"
          ),
        ],
      ],
      insuranceProviderId: ["", Validators.required],
      insuranceNumber: ["", Validators.required],
      address: ["", Validators.required],
      county: ["", [Validators.required]],
      countyName: [""],
      zip: [
        "",
        [Validators.required, Validators.minLength(5), Validators.maxLength(9)],
      ],
      reasons: this.fb.array([]),
      serviceStart: ["", Validators.required],
      preferredTiming: this.fb.array([]),
      preference: ["", Validators.required],
      providerGenderPreference: ["null"],
      counsellingMethod: ["", Validators.required],
      otherExperty: [""],
      city: ["", Validators.required],
      countryCode: ["+1"],
      source: ["", [Validators.required]],
      edUser: ["false"],
    });
  }

  /**
   * create form array control for councelling reasons
   * @param reasonsInput reasons array input
   */
  createReasonsControl(reasonsInput) {
    const arr = reasonsInput.map((reason) => {
      (this.enquiryForm.get("reasons") as FormArray).push(
        new FormControl(false)
      );
    });
  }

  /**
   * create form array control for time frames
   * @param timeFrameInput array containing time frame values
   */
  createTimeFramesControl(timeFrameInput) {
    timeFrameInput.map((timeFrame) => {
      (this.enquiryForm.get("preferredTiming") as FormArray).push(
        new FormControl(false)
      );
    });
  }
  /**
   * check for phone number and submit user enquiry form
   */
  submitEnquiry() {
    this.isSubmitted = true;
    if (
      this.isFormValid() &&
      this.enquiryForm.value.providerGenderPreference != "null"
    ) {
      // if (
      //   this.enquiryForm.get("phoneNumber").value &&
      //   this.enquiryForm.get("phoneNumber").valid &&
      //   this.enquiryForm.value.countryCode
      // ) {
      //   this.invalidPhoneNumber = false;
      //   this.sendOTP().subscribe((r) => {
      //     if (r.status === "success") {
      //       this.openModalForOTP();
      //     }
      //   });
      // } else {
      //   this.sendEnquiry();
      // }
      this.sendEnquiry();
    }
  }

  // if user enters phone number sennd enquiry
  sendOTP(): Observable<any> {
    const body = {
      phoneNumber: this.enquiryForm.value.phoneNumber,
      countryCode: this.enquiryForm.value.countryCode,
    };
    return this.patientService.sendOtpForVerification(body);
  }

  //submit enquiry form to backend
  sendEnquiry() {
    this.alterFormBody();
    delete this.enquiryForm.value.countyName;
    this.disableButton = true;
    this.patientService
      .submitEnquiry(this.customizeFormPayload(this.enquiryForm.value))
      .subscribe(
        (data) => {
          this.disableButton = this.isSubmitted = this.isOtherExperty = false;
          this.resetForm();
          this.alert.success(
            "Thank you for your request, if a provider does not contact you within 48 hours, please call 716-302-4545."
          );
        },
        (error) => {
          this.disableButton = false;
        }
      );
  }

  customizeFormPayload(value) {
    const form = new FormData();
    for (let key in value) {
      form.append(key, JSON.stringify(value[key]));
    }
    if (this.insuranceFile) {
      form.append("file", this.insuranceFile);
    }
    return form;
  }

  //open OTP verification modal
  openModalForOTP() {
    const modalRef = this.modal.open(OtpVerificationComponent, {
      windowClass: "",
    });
    const data = {
      phoneNumber: this.enquiryForm.value.phoneNumber,
      countryCode: this.enquiryForm.value.countryCode,
    };
    modalRef.componentInstance.data = data;
    modalRef.componentInstance.response.subscribe((data) => {
      if (data && data.status === "success") {
        this.sendEnquiry();
        modalRef.dismiss();
      }
    });
  }

  //check whether the enquiry form is valid or not and return
  isFormValid() {
    const forReason = this.isOtherExperty
      ? this.enquiryForm.get("otherExperty").value
        ? true
        : false
      : true;
    return this.enquiryForm.valid &&
      !this.reasonsNotValid &&
      !this.timeFrameNotValid &&
      forReason &&
      this.enquiryForm.get("providerGenderPreference").value != null
      ? true
      : false;
  }

  //check for reasonss form array whether valid or not
  get reasonsNotValid() {
    let reasons = this.enquiryForm.get("reasons").value;
    let validReasons = reasons.filter((reason) => {
      return reason != false && reason != null;
    });
    if (this.isSubmitted === true) {
      if (validReasons && validReasons.length) {
        return false;
      } else {
        return true;
      }
    }
  }

  //check for preferred timeframes form array whether valid or not
  get timeFrameNotValid() {
    let timeFrames = this.enquiryForm.get("preferredTiming").value;
    let validTimeFrames = timeFrames.filter((reason) => {
      return reason != false && reason != null;
    });
    if (this.isSubmitted === true) {
      if (validTimeFrames && validTimeFrames.length) {
        return false;
      } else {
        return true;
      }
    }
  }

  //reset all form values and form array values
  resetForm() {
    this.currReasons = [];
    this.enquiryForm.reset();
    this.enquiryForm.get("insuranceProviderId").setValue("");
    this.enquiryForm.get("serviceStart").setValue("");
    this.enquiryForm.get("preference").setValue("");
    this.enquiryForm.get("providerGenderPreference").setValue("null");
    this.enquiryForm.get("counsellingMethod").setValue("");
    this.enquiryForm.get("county").setValue("");
    this.enquiryForm.get("source").setValue("");
    this.enquiryForm.get("countryCode").setValue("+1");
    this.enquiryForm.get("edUser").setValue("false");
    this.resetInsuranceFile(this.insuranceFileInput.nativeElement);
  }

  resetInsuranceFile(element) {
    this.insuranceFile = null;
    element.value = "";
  }
  //customize form values in required form data format
  alterFormBody() {
    this.enquiryForm
      .get("edUser")
      .setValue(this.enquiryForm.value.edUser === "true");
    this.getSelectedReasons();
    this.getSelectedTimeFrames();
  }

  /**
   * get user selected reasons into array
   */
  async getSelectedReasons() {
    const mappedReasonsArr = _.map(
      this.enquiryForm.controls.reasons["controls"],
      (reason, i) => {
        return reason.value && this.masterData.data.expertise[i].id;
      }
    );
    this.selectedReasons = _.filter(mappedReasonsArr, function (reason) {
      if (reason !== false) {
        return reason;
      }
    });
    this.setFormDataVals("reasons", this.selectedReasons);
  }

  //  get and set user selected time frames into array
  getSelectedTimeFrames() {
    const mappedTimeFramesArr = _.map(
      this.enquiryForm.controls.preferredTiming["controls"],
      (timeFrame, i) => {
        return timeFrame.value && this.masterData.data.timeframes[i].id;
      }
    );
    //set and create array of only selected time frames
    this.selectedTimeFrames = _.filter(
      mappedTimeFramesArr,
      function (timeFrame) {
        if (timeFrame !== false) {
          return timeFrame;
        }
      }
    );
    this.setFormDataVals("preferredTiming", this.selectedTimeFrames);
  }

  // alter form data values with new values
  setFormDataVals(formControlName, value) {
    this.enquiryForm.value[formControlName] = value;
  }

  //return from control for error catch
  get errorControl() {
    return this.enquiryForm.controls;
  }

  // check for only number entered
  onlyNumber(event) {
    const charCode = event.query ? event.query : event.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57)) {
      return false;
    }

    return true;
  }

  //handle time frame checkboxes
  handleTimeFrameChecks(value, index) {
    if (value.id === constants.TIMEFRAME_ANYTIMEID) {
      let tempArr = [false, false, false, false, false];
      tempArr[index] = !this.enquiryForm.value.preferredTiming[index];
      (this.enquiryForm.get("preferredTiming") as FormArray).setValue(tempArr);
    } else {
      let anyTimeIndex = this.masterData.data.timeframes.findIndex((val) => {
        return val.id === constants.TIMEFRAME_ANYTIMEID;
      });
      (this.enquiryForm.get("preferredTiming") as FormArray).value[
        anyTimeIndex
      ] = false;
    }
  }

  onlyStringValidation(event) {
    var key;
    key = event.charCode; //         key = event.keyCode;  (Both can be used)
    return (key > 47 && key < 58) || key == 45 || key == 46;
  }

  //get provider preferences as per reason selected
  reasonSelected(selectedReason) {
    //check if user clicked on Others to open a freetext field
    if (
      selectedReason.expertise === constants.OTHER_EXPERTISE_VALUE ||
      selectedReason.expertise === constants.OTHERS_EXPERTISE_VALUE
    ) {
      this.checkForOtherReason();
    }
    const index = this.currReasons.findIndex((val) => {
      return val === selectedReason.id;
    });
    if (index < 0) {
      this.currReasons.push(selectedReason.id);
    } else {
      this.currReasons.splice(index, 1);
    }
    this.getProviderPreferences(this.currReasons);
  }

  //show extra free field for other reason option
  checkForOtherReason() {
    this.isOtherExperty = !this.isOtherExperty;
    this.enquiryForm.get("otherExperty").setValue("");
  }

  //get provider preferences as per the selected reasons
  getProviderPreferences(reasons) {
    this.patientService.getProviderPreferences({ reasons: reasons }).subscribe(
      (data) => {
        this.enquiryForm.get("preference").setValue("");
        this.preferences = data.data;
      },
      (error) => {
        this.enquiryForm.get("preference").setValue("");
        this.preferences = [];
      }
    );
  }

  // get county name based on  zip code
  getCounty(event) {
    if (this.enquiryForm.get("zip").valid) {
      this.enquiryForm.get("county").reset();
      this.enquiryForm.get("countyName").reset();
      this.patientService.getCountyName(event.target.value).subscribe(
        (data) => {
          if (data.data[0].county) {
            this.isZipCodeCorrect = "true";
            this.enquiryForm.get("countyName").setValue(data.data[0].county);
            this.enquiryForm.get("county").setValue(data.data[0].id);
          } else {
            this.isZipCodeCorrect = "false";
          }
        },
        (error) => {
          this.isZipCodeCorrect = "false";
        }
      );
    }
  }

  changeZipCode(event) {
    this.enquiryForm.get("countyName").reset();
    this.isZipCodeCorrect = "";
  }

  fileSelected(file) {
    let fileValidation = this.common.validateFileSize(
      file.target.files[0].size,
      config.constants.insuranceFileSizeLimit
    );
    if (!fileValidation.status) {
      this.resetInsuranceFile(this.insuranceFileInput.nativeElement);
      this.alert.error(fileValidation.message);
      return;
    }
    this.insuranceFile = file.target.files[0];
  }
}
