import { Injectable } from "@angular/core";
import { ApiService } from "./api.service";
import { Observable } from "rxjs/internal/Observable";
import { ApiEndPoints } from "../constants/api-endpoints";
import { HttpClient } from "@angular/common/http";
import { Router } from "@angular/router";

@Injectable({
  providedIn: "root",
})
export class CommonService {
  constructor(
    private api: ApiService,
    private http: HttpClient,
    private router: Router
  ) {}
  //get enquiry form master data
  getEnquiryMasterData(): Observable<any> {
    return this.http.get(ApiEndPoints.getEnquiryMasterData);
  }

  getInquiryReports(): Observable<any> {
    return this.http.get(ApiEndPoints.inquiryReports);
  }

  //delete token and logout user
  logoutUser() {
    localStorage.removeItem("mcny_user");
    this.router.navigateByUrl("login");
  }

  /**
   * restrict user from entering spaces
   * @param e key pressed by user
   */
  noWhiteSpace(e) {
    if (e.key === " ") {
      return false;
    }
  }

  /**
   * validate file size
   * @param fileSize
   * @param maxLimit
   * @returns
   */
  validateFileSize(fileSize, maxLimit) {
    if (fileSize >= maxLimit) {
      return {
        status: false,
        message: `Maximum size of file can be ${maxLimit / 1024} MB`,
      };
    }
    return { status: true };
  }
}
