import { Injectable } from "@angular/core";
import { HttpClient, HttpParams } from "@angular/common/http";
import { Observable } from "rxjs";
import { ApiEndPoints } from "src/app/core/constants/api-endpoints";

@Injectable({
  providedIn: "root",
})
export class MetaDataService {
  constructor(private http: HttpClient) {}

  // get  metadata list
  getMetaData(path, filter): Observable<any> {
    const url = ApiEndPoints[path];
    const params: HttpParams = filter;
    return this.http.get(url, { params });
  }

  // add new metadata by admin
  addMetaData(path, body): Observable<any> {
    const url = ApiEndPoints[path];
    return this.http.post(url, body);
  }

  // add new metadata by admin
  updateMetaData(id, path, body): Observable<any> {
    const url = ApiEndPoints[path];
    return this.http.put(url + `/${id}`, body);
  }

  //get metadata details form api
  getDetails(path, id): Observable<any> {
    const url = ApiEndPoints[path];
    return this.http.get(url + `/${id}`);
  }
}
