import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { DoctorListComponent } from "./components/doctor-list/doctor-list.component";
import { DoctorProfileComponent } from "./components/doctor-profile/doctor-profile.component";
import { NewRequestsComponent } from "./components/new-requests/new-requests.component";
import { MetaDataService } from "src/app/core/resolvers/meta-data.service";

const routes: Routes = [
  { path: "", component: DoctorListComponent },
  { path: "new-requests", component: NewRequestsComponent },
  {
    path: "add-clinician",
    component: DoctorProfileComponent,
    resolve: { metaData: MetaDataService },
  },
  {
    path: "edit-clinician",
    component: DoctorProfileComponent,
    resolve: { metaData: MetaDataService },
  },
  {
    path: "view-clinician",
    component: DoctorProfileComponent,
    resolve: { metaData: MetaDataService },
  },
];
@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class DoctorRoutingModule {}
