import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { ApiService } from "src/app/core/services/api.service";
import { ApiEndPoints } from "src/app/core/constants/api-endpoints";
import { HttpClient, HttpParams } from "@angular/common/http";

@Injectable({
  providedIn: "root",
})
export class DoctorService {
  constructor(private api: ApiService, private http: HttpClient) {}

  getDoctorsList(filters: HttpParams): Observable<any> {
    let params: HttpParams = filters;
    return this.http.get(ApiEndPoints.doctors, { params });
  }

  getDoctorDetails(docId): Observable<any> {
    return this.http.get(`${ApiEndPoints.doctors}/${docId}`);
  }

  addDoctor(body): Observable<any> {
    return this.http.post(`${ApiEndPoints.doctors}`, body);
  }

  updateDoctor(id, body): Observable<any> {
    return this.http.put(`${ApiEndPoints.doctors}/${id}`, body);
  }
  //get county name by zip code
  getCountyName(zipCode): Observable<any> {
    return this.http.get(`${ApiEndPoints.getCountyByZip}/${zipCode}/counties`);
  }
}
