import { Component, OnInit } from "@angular/core";
import { DoctorService } from "../../services/doctor.service";
import { filter } from "rxjs/operators";
import { AlertService } from "src/app/core/_alert";
import { SwalAlertService } from "src/app/core/services/swal-alert.service";
import { sweetAlertConfig } from "src/app/core/constants/config";

@Component({
  selector: "app-doctor-list",
  templateUrl: "./doctor-list.component.html",
  styleUrls: ["./doctor-list.component.css"],
})
export class DoctorListComponent implements OnInit {
  doctors = [];
  currentPage = 1;
  totalRecords = 0;
  searchText = "";
  constructor(
    private doctorService: DoctorService,
    private alertService: AlertService,
    public swalAlert: SwalAlertService
  ) {}

  ngOnInit() {
    this.getDoctorsList({});
  }

  //get all clinicians list as per the filters
  getDoctorsList(filters) {
    filters.verifiedByAdmin = true;
    filters.pageNumber = this.currentPage;
    filters.search = this.searchText;
    this.doctorService.getDoctorsList(filters).subscribe((data) => {
      this.doctors = data.data.doctors;
      this.totalRecords = data.data.count;
    });
  }

  // method called via event emitter from pagination component
  clickPage(pageNo) {
    this.currentPage = pageNo;
    this.getDoctorsList({});
  }

  async changeStatus(docId, status, index) {
    const confirmConfig: any = sweetAlertConfig.clinicianReqConfig;
    const action = status ? "activate" : "deactivate";
    confirmConfig.content = `<p class="fw-500"> Are you sure you want to ${action} this clinician? </p> `;
    const userOpt = await this.swalAlert.chooseOptions(confirmConfig);
    if (userOpt && userOpt.isConfirmed) {
      this.doctorService
        .updateDoctor(docId, { active: status })
        .subscribe((data) => {
          this.alertService.success(data.message);
          this.doctors[index].active = status;
        });
    }
  }

  searchDoctor() {
    this.currentPage = 1;
    this.getDoctorsList({});
  }

  clearSeachResult() {
    this.searchText = "";
    this.getDoctorsList({});
  }
}
