import { Component, OnInit, Input, EventEmitter, Output } from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { CommonService } from "src/app/core/services/common.service";
import { MetaDataService } from "../services/meta-data.service";
import { DialogService } from "../services/dialog.service";
import { AlertService } from "src/app/core/_alert";

@Component({
  selector: "app-manage-zipcodes",
  templateUrl: "./manage-zipcodes.component.html",
  styleUrls: ["./manage-zipcodes.component.css"],
})
export class ManageZipcodesComponent implements OnInit {
  form: FormGroup;
  @Input() data;
  @Output() status: EventEmitter<any> = new EventEmitter();
  currRoute = "";
  isDisabled = false;
  isSubmitted = false;
  masterData;
  constructor(
    private fb: FormBuilder,
    private common: CommonService,
    private metaService: MetaDataService,
    private dialog: DialogService,
    private alertService: AlertService
  ) {
    this.form = this.fb.group({
      zipcode: [
        "",
        [Validators.required, Validators.minLength(5), Validators.maxLength(9)],
      ],
      countyId: ["", [Validators.required]],
    });
    setTimeout(() => {
      this.currRoute = this.data.currURL;
      if (this.data.type === "EDIT") {
        this.form.get("zipcode").setValue(this.data.data.name);
        this.form.get("countyId").setValue(this.data.data.zipcodeCounty.id);
      }
    });
  }

  get f() {
    return this.form.controls;
  }

  ngOnInit() {
    this.getMasterData();
  }

  submitForm() {
    this.isSubmitted = true;
    if (this.form.valid) {
      this.isDisabled = true;
      if (this.data.type === "ADD") {
        this.addData();
      } else {
        this.updateData();
      }
    }
  }

  // add new metadata
  addData() {
    this.metaService.addMetaData(this.currRoute, this.form.value).subscribe(
      (data) => {
        this.dialog.close();
        this.status.emit(true);
        this.alertService.success(data.message);
      },
      (error) => {
        this.isDisabled = false;
      }
    );
  }

  // update the data
  updateData() {
    this.metaService
      .updateMetaData(this.data.data.id, this.currRoute, this.form.value)
      .subscribe((data) => {
        this.dialog.close();
        this.status.emit(true);
        this.alertService.success(data.message);
      }),
      (error) => {
        this.isDisabled = false;
      };
  }

  // get master data for county dropdown
  getMasterData() {
    this.common.getEnquiryMasterData().subscribe((data) => {
      this.masterData = data;
    });
  }
}
