import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { InquiryReportResolver } from "../core/resolvers/inquiry-reports";
import { MetaDataService } from "../core/resolvers/meta-data.service";
import { IndexComponent } from "./index/index.component";

const routes: Routes = [
  {
    path: "",
    redirectTo: "dashboard",
    pathMatch: "full",
  },
  {
    path: "",
    component: IndexComponent,
    children: [
      {
        path: "dashboard",
        loadChildren: () =>
          import("./dashboard/dashboard.module").then((x) => x.DashboardModule),
      },
      {
        path: "clinicians",
        loadChildren: () =>
          import("./doctor/doctor.module").then((x) => x.DoctorModule),
      },
      {
        path: "data",
        loadChildren: () =>
          import("./meta-data/meta-data.module").then((x) => x.MetaDataModule),
      },
      {
        path: "reports",
        resolve: {
          reports: InquiryReportResolver,
          metaData: MetaDataService,
        },
        loadChildren: () =>
          import("./reports/reports.module").then((x) => x.ReportsModule),
      },
    ],
  },

  {
    path: "**",
    redirectTo: "dashboard",
    pathMatch: "full",
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AdminRoutingModule {}
