import {
  ApplicationRef,
  ComponentFactoryResolver,
  ComponentRef,
  EmbeddedViewRef,
  Injectable,
  Injector,
  Type,
} from "@angular/core";
import { ModalDismissReasons, NgbModal } from "@ng-bootstrap/ng-bootstrap";

@Injectable({
  providedIn: "root",
})
export class DialogService {
  constructor(private modalService: NgbModal) {}
  dialogComponentRef: ComponentRef<any>;
  closeResult: string;
  modalReference: any;
  options: {};

  // called when need to open simple modal with no data transfer
  open(component, data) {
    const windowClass = "fade addLocationFieldModal";

    this.options = {
      backdrop: true,
      keyboard: false,
      windowClass,
    };
    this.modalReference = this.modalService.open(component, this.options);
    this.modalReference.componentInstance.data = data;

    this.modalReference.result.then(
      (result) => {
        this.closeResult = `Closed with: ${result}`;
      },
      (reason) => {
        this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
      }
    );
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      console.log("ESC");
      return "by pressing ESC";
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return "by clicking on a backdrop";
    } else {
      return `with: ${reason}`;
    }
  }

  close() {
    this.modalReference.close();
  }
}
