import { Injectable } from "@angular/core";
import { ApiService } from "src/app/core/services/api.service";
import { Observable } from "rxjs";
import { ApiEndPoints } from "src/app/core/constants/api-endpoints";
import { HttpClient } from "@angular/common/http";

@Injectable({
  providedIn: "root",
})
export class PatientService {
  constructor(private api: ApiService, private http: HttpClient) {}

  //get enquiry form master data
  getEnquiryMasterData(): Observable<any> {
    return this.http.get(ApiEndPoints.getEnquiryMasterData);
  }

  //get enquiry form master data
  getProviderPreferences(body): Observable<any> {
    return this.http.post(ApiEndPoints.getProviderPreferences, body);
  }

  //submit enquiry form for patient
  submitEnquiry(data): Observable<any> {
    return this.http.post(ApiEndPoints.submitEnquiryForm, data);
  }

  //get cities by counties
  getCities(countyId): Observable<any> {
    return this.http.get(`${ApiEndPoints.getCities}/${countyId}/cities`);
  }

  //get county name by zip code
  getCountyName(zipCode): Observable<any> {
    return this.http.get(`${ApiEndPoints.getCountyByZip}/${zipCode}/counties`);
  }

  //send otp to phone number for verification
  sendOtpForVerification(body): Observable<any> {
    return this.http.post(ApiEndPoints.sendOTP, body);
  }

  // verify otp sent to phone number
  verifyOTP(body): Observable<any> {
    return this.http.post(ApiEndPoints.verifyOTP, body);
  }
}
