import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { AuthGuard } from "./core/guard/auth.guard";
import { IsAdminLoggedGuard } from "./core/guard/is-admin-logged.guard";

const routes: Routes = [
  {
    path: "inquiry",
    loadChildren: () =>
      import("./patient-enquiry/patient-enquiry.module").then(
        (x) => x.PatientEnquiryModule
      ),
  },
  {
    path: "login",
    loadChildren: () =>
      import("./login/login.module").then((x) => x.LoginModule),
    canActivate: [IsAdminLoggedGuard],
  },
  {
    path: "admin",
    loadChildren: () =>
      import("./admin/admin.module").then((x) => x.AdminModule),
    canActivate: [AuthGuard],
  },

  { path: "**", redirectTo: "inquiry" },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})
export class AppRoutingModule {}
