import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { MetaDataService } from "../services/meta-data.service";
import { DialogService } from "../services/dialog.service";
import { ManageMetaDataComponent } from "../manage-meta-data/manage-meta-data.component";
import { ManageZipcodesComponent } from "../manage-zipcodes/manage-zipcodes.component";
import { SwalAlertService } from "src/app/core/services/swal-alert.service";
import { sweetAlertConfig } from "src/app/core/constants/config";
import { AlertService } from "src/app/core/_alert";

@Component({
  selector: "app-show-lists",
  templateUrl: "./show-lists.component.html",
  styleUrls: ["./show-lists.component.css"],
})
export class ShowListsComponent implements OnInit {
  currentPage = 1;
  totalRecords = 0;
  currURL = "";
  metaData = [];
  filters = {
    pageNumber: 1,
  };
  constructor(
    private router: Router,
    private metaService: MetaDataService,
    private dialog: DialogService,
    private alertService: AlertService,
    public swalAlert: SwalAlertService
  ) {}

  ngOnInit() {
    this.currURL = this.router.url.split("/")[3];
    this.getData();
  }

  // fet metadata list
  getData() {
    this.metaData = [];
    this.filters.pageNumber = this.currentPage;
    this.metaService
      .getMetaData(this.currURL, this.filters)
      .subscribe((data) => {
        this.metaData = data.data.data;
        this.totalRecords = data.data.count;
      });
  }

  // method called via event emitter from pagination component
  clickPage(pageNo) {
    this.currentPage = pageNo;
    this.getData();
  }

  // open modal for add/edit data
  openModal(data, type) {
    if (this.currURL === "zipcodes") {
      this.dialog.open(ManageZipcodesComponent, {
        currURL: this.currURL,
        type: type,
        data: data,
      });
    } else {
      this.dialog.open(ManageMetaDataComponent, {
        currURL: this.currURL,
        type: type,
        data: data,
      });
    }
    this.dialog.modalReference.componentInstance.status.subscribe((data) => {
      if (data !== null) {
        if (data) {
          this.getData();
        }
      }
    });
  }

  async changeStatus(data, status, index) {
    const confirmConfig: any = sweetAlertConfig.clinicianReqConfig;
    const action = status ? "activate" : "deactivate";
    confirmConfig.content = `<p class="fw-500"> Are you sure you want to ${action} <b>${data.name}</b>? </p> `;
    const userOpt = await this.swalAlert.chooseOptions(confirmConfig);
    if (userOpt && userOpt.isConfirmed) {
      let body;
      body = { name: data.name, active: status };
      if (this.currURL === "zipcodes") {
        body = {
          zipcode: data.name,
          countyId: data.zipcodeCounty.id,
          active: status,
        };
      }
      this.metaService
        .updateMetaData(data.id, this.currURL, body)
        .subscribe((data) => {
          this.metaData[index].active = status;
          this.alertService.success(data.message);
        });
    }
  }
}
